.button {
    width: 100px;
    font: inherit;
    padding: 0.5rem 1.5rem;
    border: 1px solid #8b005d;
    border-radius: 12px;
    color: white;
    background: #8b005d;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
    cursor: pointer;
    justify-content: center;
}
  
  .button:focus {
    outline: none;
  }
  
  .button:hover,
  .button:active {
    background: #ac0e77;
    border-color: #ac0e77;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
  }
  
  @media (min-width: 768px) {
    .button {
      width: auto;
    }
  }